@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.light {
  --base: #efeeee;
  --text: black;
  --light: #fff7;
  --dark: #d1cdc777;
  --border: #fff4;
  --shadow: #0002;
  --background: white;
  --accent: #61dafb;
  --alt-text: black;
  --accent-light: #92e7ff;
}

.dark {
  --base: #313130;
  --text: #dee;
  --light: #ffe1;
  --dark: #0006;
  --border: #0001;
  --shadow: #0008;
  --background: #212120;
  --accent: #61dafb;
  --alt-text: var(--base);
  --accent-light: #92e7ff;
}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 1em;
  gap: 1em;
}

button {
  margin: 0;
  padding: 0.5em;

  font-weight: 500;
  font-size: 1.5em;
  line-height: 1.5em;
  text-align: center;

  color: var(--text);

  outline: none;
  border: 0.05em solid var(--border);

  border-radius: 0.7em;
  box-shadow: 0.2em 0.2em 0.4em var(--dark), -0.2em -0.2em 0.4em var(--light);
  background-color: var(--base);

  cursor: pointer;

  transition: all 0.2s ease;
}

button:hover {
  background-color: var(--light);
}

button:active {
  box-shadow: 0.2em 0.2em 0.4em var(--dark), -0.2em -0.2em 0.4em var(--light),
    inset 0.2em 0.2em 0.4em var(--dark), inset -0.2em -0.2em 0.4em var(--light);
}

.key1 {
  grid-area: 1 / 1 / 2 / 2;
  background-color: var(--accent);
  color: var(--alt-text);
}
.key2 {
  grid-area: 2 / 4 / 4 / 5;
}
.key3 {
  grid-area: 5 / 1 / 6 / 3;
}
.key4 {
  grid-area: 4 / 4 / 6 / 5;
  background-color: var(--accent);
  color: var(--alt-text);
}
.key1:hover,
.key4:hover {
  background-color: var(--accent-light);
}

.output {
  height: 2em;
  margin-bottom: 1em;
  padding: 0 0.5em;

  font-size: 3em;
  line-height: 1.9em;
  text-align: right;

  border-bottom: solid 0.1em var(--dark);
  border-radius: 0.3em;

  background-color: var(--light);
  color: var(--text);
}

.smaller {
  height: 4em;
  margin-bottom: 2em;
  padding: 0 1em;

  font-size: 1.5em;
  line-height: 3.8em;
  text-align: right;

  border-bottom: solid 0.2em var(--dark);
  border-radius: 0.6em;

  background-color: var(--light);
}

.smallest {
  height: 6em;
  margin-bottom: 3em;
  padding: 0 1.5em;

  font-size: 1em;
  line-height: 1.5em;

  border-bottom: solid 0.3em var(--dark);
  border-radius: 0.9em;

  overflow-wrap: anywhere;

  overflow-y: scroll;
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 0.3em;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.panel {
  width: 20em;
  padding: 2em;
  border: 0.1em solid var(--border);
  border-radius: 1em;
  background-color: var(--base);

  box-shadow: 7px 21px 21px var(--shadow);
}

@media only screen and (max-width: 50em) {
  .app.light,
  .app.dark {
    --background: var(--base);
  }
  .panel {
    border: none;
    border-radius: 0;
    box-shadow: none;
  }
}

* {
  font-family: "IBM Plex Mono", monospace;
}

.app {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--background);
}

